.App {
	text-align: center;
}

* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

.container {
	width: 100;
	margin: auto;
	padding-top: 4%;
	padding-bottom: 4%;
}

.row {
	width: 100%;
	display: flex;
	flex-wrap: wrap;
	align-items: center;
}

.login-container {
	/* justify-content: center;
	height: 98%;
	padding-bottom: 1%;
	padding-top: 5%; */
	background: linear-gradient(#e6005c, #e6005c, #ffff00);
	display: flex;
	height: 800px;
	width: 200px;
	position: relative;
	justify-content: center;
	align-items: center;
}

.post {
	position: absolute;
	color: #fff;
	font-family: "Laila", serif;
}

.col-1 {
	width: 8.33%;
}
.col-2 {
	width: 16.66%;
}
.col-3 {
	width: 25%;
}
.col-4 {
	width: 33.33%;
}
.col-5 {
	width: 41.66%;
}
.col-6 {
	width: 50%;
}
.col-7 {
	width: 58.33%;
}
.col-8 {
	width: 66.66%;
}
.col-9 {
